// @mui
import { Container } from '@mui/material';
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import TransferChargesInt from '../../sections/admin/TransferChargesInt';
import useAuth from '../../hooks/useAuth';

export default function TransferCharges() {
    const { themeStretch } = useSettings();
    const { setting } = useAuth();

    return (
        <Page title="Admin || Bank Transfer Charges">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading={'Bank Transfer Charges'}
                    links={[
                        { name: 'Dashboard', href: PATH_ADMIN.root },
                        { name: 'Discount / Charges', href: PATH_ADMIN.discount.root },
                        { name: 'Transfer Charges' },
                    ]}
                />

                <TransferChargesInt setting={setting?.setting} />
            </Container>
        </Page>
    );
}
