import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField, RHFSelect } from '../../components/hook-form';
import useIsMountedRef from '../../hooks/useIsMountedRef';

// ----------------------------------------------------------------------
TransferChargesInt.propTypes = {
    setting: PropTypes.object,
};

export default function TransferChargesInt({ setting }) {
    const isMountedRef = useIsMountedRef();
    const UpdateSchema = Yup.object().shape({
        type: Yup.string().required('Charge type is required'),
        value: Yup.number().required('Charge value is required'),
        cap: Yup.number().required('Charge cap is required'),
    });

    const defaultValues = useMemo(
        () => ({
            type: setting?.transfer_charge_type || 'FLAT',
            value: setting?.transfer_charge_value || 0,
            cap: setting?.transfer_charge_cap || 0,
        }),
        [setting]
    );

    const AccessToken = window.localStorage.getItem('accessToken');
    const methods = useForm({
        resolver: yupResolver(UpdateSchema),
        defaultValues,
    });

    const {
        reset,
        setError,
        handleSubmit,
        formState: { errors, isSubmitting },
    } = methods;

    useEffect(() => {
        if (setting) {
            reset(defaultValues);
        }
    }, [setting, defaultValues, reset]);

    const onSubmit = async (data) => {
        try {
            await axios.post(`/api/secure/discount/other/${AccessToken}/habukhan/secure`, {
                id: AccessToken,
                ...data
            });
            swal('success', `Transfer Charges Updated Successfully`, 'success');
        } catch (error) {
            if (isMountedRef.current) {
                setError('afterSubmit', { ...error, message: error.message || 'Error updating charges' });
            }
        }
    };

    return (
        <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
            <Grid container spacing={3}>
                <Grid item xs={12} md={8}>
                    {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
                    <Card sx={{ p: 3 }}>
                        <Box
                            sx={{
                                display: 'grid',
                                columnGap: 3,
                                rowGap: 3,
                                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
                            }}
                        >
                            <RHFSelect name="type" label="Charge Type">
                                <option value="FLAT">Flat Fee (₦)</option>
                                <option value="PERCENTAGE">Percentage (%)</option>
                            </RHFSelect>
                            <RHFTextField name="value" label="Charge Value" type="number" />
                            <RHFTextField name="cap" label="Charge Cap (for %)" type="number" />
                        </Box>
                        <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                            <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                                Save Charges
                            </LoadingButton>
                        </Stack>
                    </Card>
                </Grid>
            </Grid>
        </FormProvider>
    );
}
